/* PlayerRegistration vertical/mobile-first form styles */
.registration-modal{
  position:fixed;inset:0;background:linear-gradient(180deg,rgba(2,6,23,0.6),rgba(2,6,23,0.85));display:flex;align-items:center;justify-content:center;z-index:9000;padding:1rem
}
.registration-content{background:linear-gradient(180deg,rgba(255,255,255,0.02),rgba(255,255,255,0.01));backdrop-filter:blur(6px);border:1px solid rgba(110,243,255,0.04);border-radius:12px;padding:1rem 1rem 1.25rem;position:relative;width:420px;max-width:96%}
.close-btn{position:absolute;right:10px;top:8px;background:transparent;border:none;color:var(--muted);font-size:1.4rem;cursor:pointer}
.registration-content h2{margin:0 0 .5rem 0;font-family:'Orbitron',sans-serif;color:var(--accent);font-size:1.1rem}
.success-message{background:linear-gradient(90deg,rgba(110,243,255,0.06),rgba(142,45,226,0.04));padding:.8rem;border-radius:8px;color:var(--muted);text-align:center}

.registration-form{display:flex;flex-direction:column;gap:.65rem;margin-top:.6rem}
.registration-form label, .registration-form .form-label{display:block;width:100%}
.form-group{display:flex;flex-direction:column}
.form-label{font-size:.85rem;color:var(--muted);margin-bottom:.35rem;display:block}
.form-input{display:block;width:100%;padding:.6rem .7rem;border-radius:8px;border:1px solid rgba(255,255,255,0.03);background:transparent;color:var(--muted);outline:none}
.form-input:focus{box-shadow:0 6px 20px rgba(110,243,255,0.06);border-color:rgba(110,243,255,0.14)}

.form-row{display:flex;gap:.5rem}
.form-group.small{flex:1}

.btn-primary{margin-top:.65rem;padding:.6rem .8rem;border-radius:8px;border:none;background:var(--accent);color:#041028;font-weight:700;cursor:pointer}
.btn-primary:active{transform:translateY(1px)}

@media (max-width:480px){
  .registration-content{width:100%;padding:12px}
  .form-row{flex-direction:column}
  .form-input{font-size:1rem;padding:.7rem}
  .registration-content h2{font-size:1rem}
}
